.message-item {
    max-width: 350px;
    display: flex;
    margin-bottom: 20px;
}
.message-item__img {
    border-radius: 100%;
    background-color: crimson;
    width: 60px;
    height: 60px;
}
.message-item__user {
    flex-shrink: 0;
    margin-right: 40px;
}
.message-item__message {
    border: 1px solid blueviolet;
    border-radius: 20px;
    padding: 10px;
    min-width: 100px;
}